import { ColumnDef, Row } from '@tanstack/react-table';
import AskForService from '../../addBeneficiaryPreview/AddBeneficiaryPopup';
import { useState } from 'react';
import { InsuranceRelatives } from '@/types/insurance.types';

// Create a separate component for the actions cell
const ActionsCell = ({ row }: { row: Row<InsuranceRelatives> }) => {
    const [askForService, setAskForService] = useState(false);

    return (
        <div className="flex flex-row justify-center gap-1">
            {/* <Link to={`/human-resources/salaries-details/${row.original.id}`}> */}
            <button className="bg-green-success-600 w-14 hover:opacity-85 rounded-lg py-[6px] text-xs font-cairo font-medium border shadow-[0px_1px_2px_0px_#1018280D] text-white-blue-50">
                موافقة
            </button>
            <button className="bg-red-error-600 w-14 hover:opacity-85 rounded-lg py-[6px] text-xs font-cairo font-medium border shadow-[0px_1px_2px_0px_#1018280D] text-white-blue-50">
                رفض
            </button>
            <button
                onClick={() => setAskForService(true)}
                className="bg-primery-blue-400 w-14 hover:opacity-85 rounded-lg py-[6px] text-xs font-cairo font-medium border shadow-[0px_1px_2px_0px_#1018280D] text-white-blue-50"
            >
                معاينة
            </button>

            <AskForService
                open={askForService}
                setAskForService={setAskForService}
                handleRefreshTable={() => row.original.refreshTable}
                data={row.original}
            />
        </div>
    );
};

export const columns: ColumnDef<InsuranceRelatives>[] = [
    {
        accessorKey: 'id',
        header: () => {
            return (
                <div className="text-sm px-4 py-3 !text-primery-blue-500 leading-[26.24px] font-semibold tracking-normal">
                    ID
                </div>
            );
        },
        cell: ({ row }) => (
            <div className="text-sm text-primery-blue-500 font-normal tracking-normal leading-[26.4px]">
                {row.original.id}
            </div>
        ),
    },
    {
        accessorKey: 'name',
        header: () => {
            return (
                <div className="text-sm px-4 py-3 !text-primery-blue-500 leading-[26.24px] font-semibold tracking-normal">
                    بيانات الموظف
                </div>
            );
        },
        cell: ({ row }) => (
            <div className="text-sm text-[#625E74] flex flex-col font-normal tracking-normal leading-[26.4px]">
                <p>{row.original?.name}</p>
                <p>كود الموظف:{row.original?.id}</p>
            </div>
        ),
    },
    {
        accessorKey: 'name',
        header: () => {
            return (
                <div className="text-sm px-4 py-3 !text-primery-blue-500 leading-[26.24px] font-semibold tracking-normal">
                    اسم المستفيد
                </div>
            );
        },
        cell: ({ row }) => (
            <div className="text-sm text-[#625E74] font-normal tracking-normal leading-[26.4px]">
                {row.original?.name}
            </div>
        ),
    },
    {
        accessorKey: 'name',
        header: () => {
            return (
                <div className="text-sm px-4 py-3 !text-primery-blue-500 leading-[26.24px] font-semibold tracking-normal">
                    رقم المشترك
                </div>
            );
        },
        cell: ({ row }) => (
            <div className="text-sm text-[#625E74] font-normal tracking-normal leading-[26.4px]">
                {/* {row.original?.} */}
            </div>
        ),
    },
    {
        accessorKey: 'name',
        header: () => {
            return (
                <div className="text-sm px-4 py-3 !text-primery-blue-500 leading-[26.24px] font-semibold tracking-normal">
                    رقم الهوية
                </div>
            );
        },
        cell: ({ row }) => (
            <div className="text-sm text-[#625E74] font-normal tracking-normal leading-[26.4px]">
                {row.original?.identity_number}
            </div>
        ),
    },
    {
        id: 'actions',
        header: () => {
            return (
                <div className="text-sm text-center px-4 py-3 !text-primery-blue-500 leading-[26.24px] font-semibold tracking-normal">
                    الاجراءات
                </div>
            );
        },
        cell: ({ row }) => <ActionsCell row={row} />,
    },
];
