import ArrowBackIcon from '@/components/icons/ArrowBackIcon';
import { Employee } from '@/types/employee.types';
import jsPDF from 'jspdf';
import autoTable from 'jspdf-autotable';

interface ExportToPDFProps {
    data: Employee[];
}

export default function ExportToPDF({ data }: ExportToPDFProps) {
    const exportToPDF = () => {
        const doc = new jsPDF();
        // doc.setFont('Amiri');
        doc.setFontSize(16);
        doc.text('customers information', 105, 20, { align: 'center' }); // Title centered
        doc.setLanguage('ar');

        // Prepare table data
        const tableData = data.map((card) => [card?.name || 'N/A', card?.email || 'N/A', card?.department || 'N/A']);

        // Generate table using autoTable
        autoTable(doc, {
            startY: 30,
            head: [['name', 'email', 'job']],
            body: tableData,
            styles: {
                // font: 'Amiri',
                halign: 'center',
            },
            headStyles: { fillColor: [41, 128, 185], textColor: 255 },
            alternateRowStyles: { fillColor: [240, 240, 240] },
        });

        // Save the PDF
        doc.save('employees_statistics.pdf');
    };

    return (
        <button
            onClick={exportToPDF}
            className="flex items-center justify-center gap-2 px-[14px] py-2 bg-primery-blue-50 border hover:bg-primery-blue-100 border-primery-blue-50 shadow-[0px_1px_2px_0px_#1018280D] rounded-lg "
        >
            <ArrowBackIcon className="text-primery-blue-700" />
            <p className="text-xs text-primery-blue-500 tracking-normal font-normal leading-[22.49px]">تصدير</p>
        </button>
    );
}
