import { Table } from '@tanstack/react-table';
import UnderlineHeading from '@/components/heading/UnderlineHeading';
import { CustomSelect } from '@/components/dropDown/Dropdown';
import { Filters } from '@/types/table.types';
import { Employee } from '@/types/employee.types';
import AddIcon from '@/components/icons/AddIcon';
import { PrimaryButton } from '@/components/buttons/PrimaryButton';
import { useState } from 'react';
import AddLetterRequest from '@/features/selfServices/components/services/popups/AddLetterRequest';
import { Link } from 'react-router-dom';
import SearchIcon from '@/components/icons/Search';
import { Input } from '@/components/ui/input';
import ExportToPDF from './ExportToPDF';

interface DataTableToolbarProps {
    table: Table<Employee>;
    globalFilter: string;
    setGlobalFilter: (value: string) => void;
    filters: Filters;
    setFilters: (value: Filters) => void;
    handleRefreshTable: () => void;
    data: any[];
}

export function DataTableToolbar({
    globalFilter,
    setGlobalFilter,
    handleRefreshTable,
    filters,
    setFilters,
    data,
}: DataTableToolbarProps) {
    const [addLetter, setAddLetter] = useState(false);

    return (
        <>
            <div className="flex flex-col p-2 sm:flex-row sm:items-center sm:justify-between">
                <UnderlineHeading text="قائمة الطلبات " />
                <div className="flex flex-row items-center gap-4">
                    <div className="flex flex-row items-center w-[287px] border border-white-blue-300 bg-white-blue-50 h-[36px] p-2 rounded-[6px]">
                        <Input
                            placeholder="ابحث باسم المستفيد"
                            value={globalFilter}
                            onChange={(event) => setGlobalFilter(event.target.value)}
                            className="text-white-blue-800 p-0 tracking-normal text-xs lg:text-sm lg:leading-[16.8px] border-none outline-none focus-visible:ring-0 placeholder:text-white-blue-800"
                        />
                        <button className="bg-transparent border-none">
                            <SearchIcon className="text-white-blue-800" />
                        </button>
                    </div>
                    <div className="flex fle-row items-center gap-4">
                        <CustomSelect
                            value={(filters?.status as string) || ''}
                            onValueChange={(value) => {
                                setFilters({ ...filters, status: value });
                            }}
                            placeholder="فلتر"
                            width="min-w-[77px]"
                            options={[
                                { label: 'فلتر', value: '' },
                                { label: 'متاح', value: 'active' },
                                { label: 'غير متاح', value: 'not active' },
                            ]}
                        />
                    </div>
                    {/* <div>
                        <Link to="/cars/add-booking">
                            <PrimaryButton className="text-base px-[14px] xl:py-2 leading-5 font-semibold font-cairo tracking-normal">
                                <AddIcon />
                                <p className="text-xs tracking-normal font-normal leading-[22.49px]">إضافة حجز</p>
                            </PrimaryButton>
                        </Link>
                    </div> */}

                    <ExportToPDF data={data} />
                </div>
            </div>
            <AddLetterRequest open={addLetter} setAddLetter={setAddLetter} handleRefreshTable={handleRefreshTable} />
        </>
    );
}
