import UnderlineHeading from '@/components/heading/UnderlineHeading';
import { CustomSelect } from '@/components/dropDown/Dropdown';
import MultipleBarChart from '@/components/charts/MultipleBarChart';
import { ChartConfig } from '@/components/ui/chart';
import { NewCustomersStats } from '@/features/customerRelations/types/newUsersStats.types';

interface Props {
    stats: NewCustomersStats | null;
    setRequsestReportsStatsPeriod: (period: string) => void;
    requsestReportsStatsPeriod: string;
}

const RequestReportsStatistics = ({ stats, requsestReportsStatsPeriod, setRequsestReportsStatsPeriod }: Props) => {
    let ChartData: any[] = [];

    if (stats?.new_customers) {
        ChartData = Object.entries(stats?.new_customers).map(([key, value]) => ({
            days: key,
            visitors: value,
        }));
    }    

    const ChartConfig = {
        visitors: {
            label: 'تقرير الطلبات',
            color: 'hsl(var(--primery-blue-500))',
        },
    } satisfies ChartConfig;
    return (
        <div className="shadow-[0px_2px_12px_0px_#14142B14] rounded-[0.5rem]">
            <div className="px-4 pt-4 flex flex-row justify-between mb-10">
                <UnderlineHeading text="تقرير الطلبات" />
                <CustomSelect
                    options={[
                        { label: 'يومي', value: 'يومي' },
                        { label: 'أسبوعي ', value: 'أسبوعي ' },
                        { label: 'شهري', value: 'شهري' },
                        { label: 'سنوي', value: 'سنوي' },
                    ]}
                    value={requsestReportsStatsPeriod}
                    onValueChange={setRequsestReportsStatsPeriod}
                    placeholder="القائمة"
                    width="min-w-[101px]"
                />
            </div>
            <div className="ps-6 pb-4">
                <MultipleBarChart chartConfig={ChartConfig} chartData={ChartData} barLabelColor="#fff" />
            </div>
        </div>
    );
};

export default RequestReportsStatistics;
