import { z } from 'zod';

export const AskForServiceSchema = z.object({
    name: z.string().optional(),
    id: z.number().optional(),
    identity_number: z.string().optional(),
    birth_date: z
        .date({
            required_error: 'This field is required',
        })
        .optional(),
    note: z.string().optional(),
    // attachment: z.instanceof(File, { message: 'File is required' }).optional(),
    reject_reason: z.string().optional(),
    reject_attachment: z.instanceof(File, { message: 'File is required' }).optional(),
});

export type AskForServiceFormValues = z.infer<typeof AskForServiceSchema>;
