import { Dialog, DialogContent, DialogHeader } from '@/components/ui/dialog';
import { cn } from '@/lib/utils';
import AskForServiceForm from '../../forms/AskForServiceForm';
import { InsuranceRelatives } from '@/types/insurance.types';

interface Props {
    open: boolean;
    setAskForService: (value: boolean) => void;
    handleRefreshTable: () => void;
    data: InsuranceRelatives;
}

export default function AskForService({ open, setAskForService, handleRefreshTable, data }: Props) {
    return (
        <Dialog open={open}>
            <div
                className={cn('fixed inset-0 bg-black/50', {
                    '!hidden': !open,
                })}
            />
            <DialogContent
                showCloseButton={false}
                className="max-w-[623px] rounded-lg p-8 bg-white-blue-50 font-cairo gap-0 [&_[aria-label=Close]]:opacity-0"
            >
                <DialogHeader className="flex flex-col items-center">
                    <div className="flex flex-row justify-between mb-4 w-full">
                        <div className="flex flex-row gap-2">
                            <svg
                                width="25"
                                height="25"
                                viewBox="0 0 25 25"
                                fill="none"
                                xmlns="http://www.w3.org/2000/svg"
                            >
                                <path
                                    d="M22.4688 18.5V19.5C22.4688 21.15 22.4688 22.5 19.4688 22.5H5.46875C2.46875 22.5 2.46875 21.15 2.46875 19.5V18.5C2.46875 17.95 2.91875 17.5 3.46875 17.5H21.4688C22.0187 17.5 22.4688 17.95 22.4688 18.5Z"
                                    fill="#145DA0"
                                />
                                <path
                                    opacity="0.4"
                                    d="M21.2195 13.5V17.5H3.76953V13.5C3.76953 9.66 6.47953 6.45 10.0895 5.68C10.6295 5.56 11.1895 5.5 11.7695 5.5H13.2195C13.7995 5.5 14.3695 5.56 14.9095 5.68C18.5195 6.46 21.2195 9.66 21.2195 13.5Z"
                                    fill="#125592"
                                />
                                <path
                                    d="M15 5C15 5.24 14.97 5.46 14.91 5.68C14.37 5.56 13.8 5.5 13.22 5.5H11.77C11.19 5.5 10.63 5.56 10.09 5.68C10.03 5.46 10 5.24 10 5C10 3.62 11.12 2.5 12.5 2.5C13.88 2.5 15 3.62 15 5Z"
                                    fill="#145DA0"
                                />
                                <path
                                    d="M15.5 12.25H9.5C9.09 12.25 8.75 11.91 8.75 11.5C8.75 11.09 9.09 10.75 9.5 10.75H15.5C15.91 10.75 16.25 11.09 16.25 11.5C16.25 11.91 15.91 12.25 15.5 12.25Z"
                                    fill="#145DA0"
                                />
                            </svg>

                            <h3 className="text-[22px] font-bold text-primery-blue-500">طلب خدمة</h3>
                        </div>
                        {/* <SubtleButton className="bg-primery-blue-50 text-base font-medium leading-[125%] text-primery-blue-500 border border-primery-blue-50">
                            <DocumentUpload />
                            لرفع مستند
                        </SubtleButton> */}
                    </div>
                    <div className="w-full border-b border-[#D1D1D1] !mt-0"></div>
                </DialogHeader>
                <AskForServiceForm setAskForService={setAskForService} handleRefreshTable={handleRefreshTable} beneficiary={data} />
            </DialogContent>
        </Dialog>
    );
}
