import { ColumnDef, Row } from '@tanstack/react-table';
import { cn } from '@/lib/utils';
import { Link } from 'react-router-dom';
import { Loan } from '@/types/loan.types';

// Create a separate component for the actions cell
const ActionsCell = ({ row }: { row: Row<Loan> }) => {
    return (
        <div className="flex flex-row justify-center gap-1">
            {/* <Link to={`/human-resources/salaries-details/${row.original.id}`}> */}
            <button className="bg-green-success-600 w-14 hover:opacity-85 rounded-lg py-[6px] text-xs font-cairo font-medium border shadow-[0px_1px_2px_0px_#1018280D] text-white-blue-50">
                موافقة
            </button>
            <button className="bg-red-error-600 w-14 hover:opacity-85 rounded-lg py-[6px] text-xs font-cairo font-medium border shadow-[0px_1px_2px_0px_#1018280D] text-white-blue-50">
                رفض
            </button>
            <Link to={`/human-resources/self-services/loan-request-preview/${row.original.id}`}>
                <button className="bg-primery-blue-400 w-14 hover:opacity-85 rounded-lg py-[6px] text-xs font-cairo font-medium border shadow-[0px_1px_2px_0px_#1018280D] text-white-blue-50">
                    معاينة
                </button>
            </Link>
        </div>
    );
};

export const columns: ColumnDef<Loan>[] = [
    {
        accessorKey: 'id',
        header: () => {
            return (
                <div className="text-sm px-4 py-3 !text-primery-blue-500 leading-[26.24px] font-semibold tracking-normal">
                    ID
                </div>
            );
        },
        cell: ({ row }) => (
            <div className="text-sm text-primery-blue-500 font-normal tracking-normal leading-[26.4px]">
                {/* {row.original.main_salary} */}
                {row.original.id}
            </div>
        ),
    },
    {
        accessorKey: 'name',
        header: () => {
            return (
                <div className="text-sm px-4 py-3 !text-primery-blue-500 leading-[26.24px] font-semibold tracking-normal">
                    بيانات الموظف
                </div>
            );
        },
        cell: ({ row }) => (
            <div className="text-sm text-[#625E74] flex flex-col font-normal tracking-normal leading-[26.4px]">
                <p>{row.original?.employee?.name}</p>
                <p>كود الموظف:{row.original?.employee_id}</p>
            </div>
        ),
    },
    {
        accessorKey: 'name',
        header: () => {
            return (
                <div className="text-sm px-4 py-3 !text-primery-blue-500 leading-[26.24px] font-semibold tracking-normal">
                    الوظيفة
                </div>
            );
        },
        cell: ({ row }) => (
            <div className="text-sm text-[#625E74] font-normal tracking-normal leading-[26.4px]">
                {/* {format(row.original.letter_for, 'yyyy-MM-dd')} */}
            </div>
        ),
    },
    {
        accessorKey: 'name',
        header: () => {
            return (
                <div className="text-sm px-4 py-3 !text-primery-blue-500 leading-[26.24px] font-semibold tracking-normal">
                    نوع السلفة
                </div>
            );
        },
        cell: ({ row }) => (
            <div className="text-sm text-[#625E74] font-normal tracking-normal leading-[26.4px]">
                {row.original.loan_type}
            </div>
        ),
    },
    {
        accessorKey: 'name',
        header: () => {
            return (
                <div className="text-sm px-4 py-3 !text-primery-blue-500 leading-[26.24px] font-semibold tracking-normal">
                    المبلغ
                </div>
            );
        },
        cell: ({ row }) => (
            <div className="text-sm text-[#625E74] font-normal tracking-normal leading-[26.4px]">
                {row.original?.amount}
            </div>
        ),
    },
    {
        accessorKey: 'service',
        header: () => {
            return (
                <div className="text-sm px-4 py-3 !text-primery-blue-500 leading-[26.24px] font-semibold tracking-normal">
                    الحالة
                </div>
            );
        },
        cell: ({ row }) => {
            return (
                <div className="text-xs font-medium tracking-normal leading-[115%]">
                    <p
                        className={cn('p-1 w-fit rounded-sm', {
                            'bg-[#DAF8E6] text-[#1A8245]': row.original.status === 'approved',
                            'bg-[#FFFBEB] text-[#D97706]': row.original.status === 'pending',
                            'bg-[#FEEBEB] text-[#E10E0E]': row.original.status === 'rejected',
                        })}
                    >
                        {row.original.status}
                    </p>
                </div>
            );
        },
    },
    {
        id: 'actions',
        header: () => {
            return (
                <div className="text-sm text-center px-4 py-3 !text-primery-blue-500 leading-[26.24px] font-semibold tracking-normal">
                    الاجراءات
                </div>
            );
        },
        cell: ({ row }) => <ActionsCell row={row} />,
    },
];
