import { Form } from '@/components/ui/form';
import { useForm } from 'react-hook-form';
import { zodResolver } from '@hookform/resolvers/zod';
import { apiPost } from '@/lib/axios/axios-utils';
import { endpoints } from '@/lib/axios/axios-global';
import { useMemo, useState } from 'react';
import { PrimaryButton } from '@/components/buttons/PrimaryButton';
import { toast } from 'sonner';
import TextInputForm from '@/components/forms/TextInputForm';
import TextAreaInputForm from '@/components/forms/TextAreaInputForm';
import FileInput from './FileInput';
import { useNavigate } from 'react-router-dom';
import { DateInputForm } from '@/components/forms/DateInputForm';
import { AskForServiceFormValues, AskForServiceSchema } from './schemas/ask-for-service-schema';
import { InsuranceRelatives } from '@/types/insurance.types';
import { SubtleButton } from '@/components/buttons/SubtleButton';
import { formatDate } from 'date-fns';

interface Props {
    beneficiary: InsuranceRelatives | null;
    setAskForService: (value: boolean) => void;
    handleRefreshTable: () => void;
}

export default function AskForServiceForm({ beneficiary, setAskForService, handleRefreshTable }: Props) {
    const [error, setError] = useState<string | null>(null);
    const [loading, setLoading] = useState(false);
    const navigate = useNavigate();

    const defaultValues = useMemo(
        () => ({
            name: beneficiary?.name || '',
            id: beneficiary?.id || '',
            identity_number: beneficiary?.identity_number || '',
            birth_date: beneficiary?.birth_date ? new Date(beneficiary.birth_date) : '',
            note: beneficiary?.note || '',
            attachment: beneficiary?.attachment || null,
        }),
        [beneficiary],
    );

    const form = useForm<AskForServiceFormValues>({
        resolver: zodResolver(AskForServiceSchema),
        defaultValues,
    });

    const onSubmit = async (data: AskForServiceFormValues) => {
        setLoading(true);
        setError(null); // Clear previous errors

        try {
            await apiPost({
                endpoint: beneficiary
                    ? endpoints.insuranceRelatives.update + beneficiary.id
                    : endpoints.insuranceRelatives.create,
                body: {
                    ...data,
                    birth_date: data?.birth_date ? formatDate(data.birth_date, 'yyyy-MM-dd') : '',
                    status: 'approved',
                },
                config: { headers: { 'Content-Type': 'multipart/form-data' } },
            });

            toast('تمت الموافقة بنجاح');
            setAskForService(false);
            handleRefreshTable();
            navigate('/human-resources/self-services?beneficiaries=true');
        } catch (error: any) {
            if (error.response?.data?.errors) {
                // Handle field-specific errors
                Object.entries(error.response.data.errors).forEach(([field, messages]) => {
                    form.setError(field as any, {
                        type: 'manual',
                        message: (messages as string[]).join(', '),
                    });
                });
            } else {
                // Handle generic error
                let errorMessage = 'حدث خطأ أثناء إرسال البيانات. يرجى المحاولة مرة أخرى.';
                if (error.response) {
                    errorMessage = error.response.data.msg || errorMessage;
                } else if (error.request) {
                    errorMessage = 'تعذر الاتصال بالخادم. يرجى التحقق من اتصالك بالإنترنت.';
                }
                setError(errorMessage);
            }
        } finally {
            setLoading(false);
        }
    };

    const rejectRequest = async () => {
        setLoading(true);
        setError(null); // Clear previous errors

        try {
            await apiPost({
                endpoint: endpoints.insuranceRelatives.update + beneficiary?.id,
                body: {
                    status: 'rejected',
                    reject_reason: form.getValues('reject_reason'),
                    reject_attachment: form.getValues('reject_attachment'),
                },
                config: { headers: { 'Content-Type': 'multipart/form-data' } },
            });

            toast('تمت رفض الطلب بنجاح');
            setAskForService(false);
            handleRefreshTable();
            navigate('/human-resources/self-services?beneficiaries=true');
        } catch (error: any) {
            // Handle generic error
            let errorMessage = 'حدث خطأ أثناء إرسال البيانات. يرجى المحاولة مرة أخرى.';
            if (error.response) {
                errorMessage = error.response.data.msg || errorMessage;
            } else if (error.request) {
                errorMessage = 'تعذر الاتصال بالخادم. يرجى التحقق من اتصالك بالإنترنت.';
            }
            setError(errorMessage);
        } finally {
            setLoading(false);
        }
    };

    return (
        <>
            <Form {...form}>
                <form onSubmit={form.handleSubmit(onSubmit)} className="mt-6">
                    {error && (
                        <div className="mb-4 p-4 bg-red-100 border border-red-400 text-red-700 rounded-lg">
                            <div className="flex justify-between items-center">
                                <span className="">{error}</span>
                                <button onClick={() => setError(null)} className="text-red-700 hover:text-red-900">
                                    ×
                                </button>
                            </div>
                        </div>
                    )}
                    <div className="grid grid-cols-1 md:grid-cols-2 gap-x-6">
                        <TextInputForm disabled name="name" label="الاسم" placeholder="" />
                        <TextInputForm disabled name="id" label="رقم المشترك" placeholder="" />
                        <TextInputForm disabled name="identity_number" label="رقم الهوية" placeholder="" />
                        <DateInputForm disabledDate name="birth_date" label="تاريخ الميلاد" placeholder="" />
                    </div>
                    <TextAreaInputForm
                        className="flex flex-col min-h-[130px]"
                        name="note"
                        label="ملاحظات اضافية"
                        placeholder=""
                        disabled
                    />
                    <FileInput name="attachment" label="رفع ملف" multiple={false} maxSize={10 * 1024 * 1024} />
                    <div className="flex flex-row justify-end gap-4 mt-6">
                        <SubtleButton
                            type="button"
                            className="text-base bg-red-error-50 !px-16 text-[#A21515] leading-5 hover:bg-red-error-50 hover:opacity-80 tracking-normal py-[10px] font-medium"
                            onClick={rejectRequest}
                        >
                            رفض
                        </SubtleButton>
                        <PrimaryButton
                            type="submit"
                            className="text-base leading-5 !px-28 tracking-normal font-medium py-[10px]"
                        >
                            {loading ? 'جاري الموافقة...' : 'موافقة'}
                        </PrimaryButton>
                    </div>
                </form>
            </Form>
        </>
    );
}
