import StatisticsCards from '@/components/cards/StatisticsCards';
import GroupIcon from '@/components/icons/user/GroupIcon';
import MinusUserIcon from '@/components/icons/user/MinusUserIcon';
import TwoUsersIcon from '@/components/icons/user/TwoUsersIcon';
import UserRemoveIcon from '@/components/icons/UserRemoveIcon';
import { employeesStats } from '@/features/humanResources/types/employeesStats.types';
import { endpoints } from '@/lib/axios/axios-global';
import { apiGet } from '@/lib/axios/axios-utils';
import { Card } from '@/types/statistics';
import { useEffect, useState } from 'react';

export default function RequestsListStatistics() {
    const [stats, setStats] = useState<employeesStats | null>(null);

    useEffect(() => {
        const fetchStats = async () => {
            try {
                const res = await apiGet({ endpoint: endpoints.employee.dashboard });
                setStats(res.data.data);
            } catch (err) {
                setStats(null);
            }
        };
        fetchStats();
    }, []);

    const cards: Card[] = [
        { text: 'عدد الطلبات', num: stats?.total_employees, percent: 12, icon: <GroupIcon /> },
        { text: 'الطلبات المقبوله', num: stats?.total_full_time, percent: -0.3, icon: <TwoUsersIcon /> },
        { text: 'الطلبات المرفوضه', num: stats?.total_part_time, percent: -0.3, icon: <MinusUserIcon /> },
        { text: 'الطلبات المعلقة', num: 35, percent: 0, icon: <UserRemoveIcon /> },
    ];

    return <StatisticsCards cards={cards} numJustifyStart={true} />;
}
