import { useEffect, useState } from 'react';
import { apiGet } from '@/lib/axios/axios-utils';
import { endpoints } from '@/lib/axios/axios-global';
import { NewCustomersStats } from '@/features/customerRelations/types/newUsersStats.types';
import RequestReportsStatistics from './RequestReportsStatistics';

export default function ChartComponent() {
    const [requsestReportsStats, setRequsestReportsStats] = useState<NewCustomersStats | null>(null);
    const [requsestReportsStatsPeriod, setRequsestReportsStatsPeriod] = useState('daily');

    useEffect(() => {
        const fetchStats = async () => {
            try {
                const res = await apiGet({
                    endpoint: endpoints.customer.dashboardNewCustomers,
                    queryParams: { period: requsestReportsStatsPeriod },
                });
                setRequsestReportsStats(res.data.data);
            } catch (err) {
                setRequsestReportsStats(null);
            }
        };
        fetchStats();
    }, [requsestReportsStatsPeriod]);

    return (
        <div className="mt-6 grid grid-cols-1">
            <RequestReportsStatistics
                stats={requsestReportsStats}
                requsestReportsStatsPeriod={requsestReportsStatsPeriod}
                setRequsestReportsStatsPeriod={setRequsestReportsStatsPeriod}
            />
        </div>
    );
}
